@import url('https://fonts.googleapis.com/css?family=Exo+2');
.intro {
    height: 100%;
    width: 100%;
    margin: auto;
    display: table;
    font-family: 'Exo 2', sans-serif;
    border-bottom: 5px solid #00af83;
    outline: none;
}

.intro .inner {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
}

.content {
    width: 80%;
    height: auto;
    margin: 0 auto;
    text-align: center;
}

.content h1 {
    font-family: 'Baron Neue',sans-serif;
    font-size: 4rem;
    color: #00af83;
    margin-top: 8rem;
}

.content h1 i {
    color: #00af83;
    font-size: 6rem;
    width: 100%;
}

.content h2 {
    margin: 2.5rem 0 6rem 0;
    font-weight: 100;
}

.skills-container {
    display: flex;
    flex-flow: row wrap;
    width: 95%;
    align-content: center;
    justify-content: space-evenly;
    margin: 1.8rem auto;
    max-width: 750px;
}

.skills-container .skill-cards {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    box-sizing: border-box;
    min-width: 120px;
}

.skills-container .skill-cards:hover *{
    color: #00af83;
}

.skills-container .skill-cards i {
    font-size: 3rem;
    transition: all ease 300ms;
}

.skills-container .skill-cards h3 {
    font-size: 1.2rem;
    font-weight: 100;
    margin: auto;
    transition: all ease 300ms;
}

.arrow.bounce a { 
    color: rgb(216, 216, 216); 
    text-decoration: none;
    transition: color ease 300ms;
}

.arrow.bounce a:hover { 
    color: #00af83; 
}

.arrow {
    text-align: center;
    padding: 7% 0;
}
.bounce {
    -moz-animation: bounce 2s infinite;
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
}
  
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
}

/* Media Queries */
@media screen and (max-width: 900px){
    .content {
        padding-bottom: 8%;
    }
    .content h1 {
        font-size: 3.5rem;
        margin-top: 7rem;
    }
    .content h1 i {
        font-size: 5rem;
    }
    .content h2 {
        margin-bottom: 5rem;
    }
    .skills-container {
        margin: 1.5rem auto;
    }
    .skills-container .skill-cards {
        margin: 3px;
        min-width: 80px;
    }
    .skills-container .skill-cards i {
        font-size: 2.7rem;
    }
    .skills-container .skill-cards h3 {
        font-size: 1.1rem;
    }
}

@media screen and (max-width: 768px){
    .content {
        padding-bottom: 10%;
    }
    .content h1 {
        font-size: 3.0rem;
        margin-top: 6rem;
    }
    .content h1 i {
        font-size: 4rem;
    }
    .content h2 {
        margin-bottom: 4rem;
    }
    .skills-container {
        margin: 1.3rem auto;
    }
    .skills-container .skill-cards i {
        font-size: 2.4rem;
    }
    .skills-container .skill-cards h3 {
        font-size: 1.0rem;
    }
}

@media screen and (max-width: 400px){
    .content {
        padding-bottom: 12%;
    }
    .content h1 {
        font-size: 2.5rem;
        margin-top: 5rem;
    }
    .content h1 i {
        font-size: 4rem;
    }
    .content h2 {
        margin-bottom: 3rem;
    }
    .skills-container {
        margin: 1.2rem auto;
    }
    .skills-container .skill-cards i {
        font-size: 2.1rem;
    }
    .skills-container .skill-cards h3 {
        font-size: 0.9rem;
    }
}